import axios from "axios";
import { getCookie } from 'cookie/cookie'
import * as infoactions from "modules/infomation/actions"

const LOAD_PUT_REQUEST = "learning-portal/src/ui/LOAD_PUT_REQUEST";
const LOAD_PUT_SUCCESS = "learning-portal/src/ui/LOAD_PUT_SUCCESS";
const LOAD_PUT_FAILURE = "learning-portal/src/ui/LOAD_PUT_REQUEST";

function loadPUT(updateInfo: any) {
    return async (dispatch: any) => {
        dispatch({ type: LOAD_PUT_REQUEST });
        try {
            const id = getCookie('id')
            const token = getCookie('token')
            const data = await axios.put(`/Users/${id}/Password`, updateInfo, {
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
            });
            dispatch({
                type: LOAD_PUT_SUCCESS,
                payload: {
                    message: "à¹à¸à¹‰à¹„à¸‚à¸£à¸«à¸±à¸ªà¸œà¹ˆà¸²à¸™à¹€à¸£à¸µà¸¢à¸šà¸£à¹‰à¸­à¸¢à¹€à¹€à¸¥à¹‰à¸§",
                    data: data.data,
                    status: data.status
                },
            });
            const action = infoactions.loadMessage("à¹à¸à¹‰à¹„à¸‚à¸£à¸«à¸±à¸ªà¸œà¹ˆà¸²à¸™à¹€à¸£à¸µà¸¢à¸šà¸£à¹‰à¸­à¸¢à¹€à¹€à¸¥à¹‰à¸§", "success")
            dispatch(action)
        } catch (err) {
            dispatch({
                type: LOAD_PUT_FAILURE,
                payload: {
                    message: "à¹€à¸à¸´à¸”à¸‚à¹‰à¸­à¸œà¸´à¸”à¸žà¸¥à¸²à¸”",
                    staus: err.response.status
                },
            });
            if (err.response.status === 401) {
                const action = infoactions.loadMessage("à¸£à¸«à¸±à¸ªà¸œà¹ˆà¸²à¸™à¹€à¸”à¸´à¸¡à¹„à¸¡à¹ˆà¸–à¸¹à¸à¸•à¹‰à¸­à¸‡", "error")
                dispatch(action)
            }
            else {

                const action = infoactions.loadMessage(`à¹€à¸à¸´à¸”à¸‚à¹‰à¸­à¸œà¸´à¸”à¸žà¸¥à¸²à¸”à¹ƒà¸™à¸à¸²à¸£à¹à¸à¹‰à¹„à¸‚à¸£à¸«à¸±à¸ªà¸œà¹ˆà¸²à¸™ ${err.response.status}`, "error")
                dispatch(action)

            }


        }
    };
}

export {
    LOAD_PUT_REQUEST,
    LOAD_PUT_SUCCESS,
    LOAD_PUT_FAILURE,
    loadPUT,
};
